<script>
  export default {
    data () {
      return {
        name: '',
        code: '',
        headers: [
          { text: 'Code', align: 'start', value: 'code', },
          { text: 'Date', value: 'date' },
          { text: 'Name', value: 'name' },
          { text: 'Product', value: 'product' },
          { text: 'Charge', value: 'charge' },
          { text: 'Hoarding Credit', value: 'hoarding' },
          { text: 'Payment', value: 'payment' },
          { text: 'Deposited', value: 'deposited' },
          { text: 'Transaction ID', value: 'id' },
          { text: 'Info', value: 'actions', sortable: false },
        ],
        dateStart: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
        dateEnd: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
        menuStart: false,
        menuEnd: false,
        dialog: false,
        titleDialogExcel: 'Do you want to get the entire data into Excel?',
        dialogExcel: false,
        transactionId: '',
        nameDialog: '',
        datePayment: '',
        productStatus: '',
        chargingCredit: '',
        hoardingCredit: '',
        methodPayment: '',
        amountPayment: ''
      }
    },

    methods: {
      resetInput() {
        let inputCheckbox = document.querySelectorAll('.table_head .v-input--selection-controls__input input')

        inputCheckbox.forEach(element => {
          if (element.checked) {
            element.click()
          }
        })

        this.name = ''
        this.code = ''
        this.dateStart = (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)
        this.dateEnd = (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)
      },
      showDialog (item) {
        this.dialog = true
        
        this.transactionId = item.id
        this.nameDialog = item.name
        this.datePayment = item.date
        this.productStatus = item.product
        this.chargingCredit = item.charge
        this.hoardingCredit = item.hoarding
        if (item.method === '') {
          this.methodPayment = '-'
        } else {
          this.methodPayment = item.method
        }
        if (item.method === '') {
          this.amountPayment = '-'
        } else {
          this.amountPayment = item.payment
        }
      },
      closeDialog () {
        this.dialog = false
      },
      showDialogExcel () {
        let isChecked = document.querySelector('.table_checkbox_current input')
        if (isChecked.checked) {
          this.titleDialogExcel = 'Do you want to get the current search results in Excel?'
        } else {
          this.titleDialogExcel = 'Do you want to get the entire data into Excel?'
        }
        this.dialogExcel = true
      },
      closeDialogExcel () {
        this.dialogExcel = false
      },
      downloadExcel(content, contentType, fileName) {
        // var link = document.createElement("a")
        // link.href = window.URL.createObjectURL(new Blob([content], {type: contentType}))
        // link.download = fileName
        // link.click()
      }
    },
  }
</script>

